import React from 'react';
import './App.css';
import Spinner from './Spinner.png';
class ErrorBoundry extends React.Component{
    componentDidCatch(error,info){
        console.log(error,info);
    }
    render(){
        return this.props.children
    }
}

class SearchBox extends React.Component{
    render(){
        return <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" onChange={this.props.action}/>
    }
}
class Card extends React.Component{
    render(){
        return(
            <div className="card">
                <img className="card-img-top" src={`https://robohash.org/${this.props.username}?size=300x300`} alt="Ropot" />
                <div className="card-body">
                    <p className="card-text">{this.props.name}</p>
                    <p className="card-text">{this.props.mail}</p>
                </div>
            </div>
        )
    }
}
class CardList extends React.Component{
    render(){
        return(
            <div className="list">
                {this.props.users.map(user=>{
                    return <Card name={user.name} mail={user.email} username={user.username} key={user.id} />
                })}
            </div>
        );
    }
}

class App extends React.Component{
    constructor(){
        super()
        this.state={
            robots:'',
            SearchValue:'',
        }
    }
    componentDidMount(){
        fetch("https://jsonplaceholder.typicode.com/users").then(Response=>Response.json()).then(users=>this.setState({robots:users}));
    }
    onSearch=(event)=>{
        this.setState({SearchValue:event.target.value});
    }
    render(){
        if (this.state.robots.length===10){
            let filterdRobot=this.state.robots.filter(user=>{
                return user.name.toLowerCase().includes(this.state.SearchValue.toLowerCase()) });
            return(
                <ErrorBoundry>
                <div>
                    <nav className="navbar">
                        <h1 className="navbar-brand">RoboCops</h1>
                        <form className="form-inline">
                            <SearchBox action={this.onSearch}/>
                        </form>
                    </nav>
                    <CardList users={filterdRobot} />
                </div>
                </ErrorBoundry>
                );
        }else {
            return (
                <div id='page'>
                    <img src={Spinner} alt="loading" id="load"/>
                </div>
            )
        }
    }
}

export default App;